"use client";

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Invoice } from '@/types/invoice';

interface InvoiceGeneratorProps {
  orderId: string;
  onInvoiceGenerated?: (invoice: Invoice) => void;
}

export default function InvoiceGenerator({ orderId, onInvoiceGenerated }: InvoiceGeneratorProps) {
  const [isGenerating, setIsGenerating] = useState(false);
  const [invoice, setInvoice] = useState<Invoice | null>(null);
  const [customerInfo, setCustomerInfo] = useState({
    name: 'Juan P√©rez',
    email: 'juan@example.com',
    phone: '+593 99 123 4567',
    address: 'Av. 6 de Diciembre 123, Quito, Pichincha',
    cedula: '1234567890',
    ruc: ''
  });

  // Datos de ejemplo del pedido
  const sampleOrder = {
    id: orderId,
    userId: 'user123',
    userName: 'Juan P√©rez',
    userEmail: 'juan@example.com',
    userPhone: '+593 99 123 4567',
    items: [
      {
        productId: '1',
        productName: 'Costillas BBQ Cl√°sicas',
        price: 18.99,
        quantity: 2
      },
      {
        productId: '2',
        productName: 'Pollo a la Parrilla',
        price: 15.50,
        quantity: 1
      }
    ],
    subtotal: 53.48,
    tax: 6.42,
    deliveryFee: 2.00,
    total: 61.90,
    paymentMethod: 'datafast',
    deliveryAddress: {
      street: 'Av. 6 de Diciembre 123',
      city: 'Quito',
      province: 'Pichincha'
    }
  };

  const handleGenerateInvoice = async () => {
    setIsGenerating(true);
    try {
      const response = await fetch('/api/invoices/generate', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          order: sampleOrder,
          customerInfo
        }),
      });

      const data = await response.json();
      
      if (data.success) {
        setInvoice(data.invoice);
        onInvoiceGenerated?.(data.invoice);
        
        // Abrir factura en nueva ventana
        const newWindow = window.open('', '_blank');
        if (newWindow) {
          newWindow.document.write(data.invoiceHTML);
          newWindow.document.close();
        }
      }
    } catch (error) {
      console.error('Error generating invoice:', error);
    } finally {
      setIsGenerating(false);
    }
  };

  const handleDownloadPDF = () => {
    if (invoice) {
      // Aqu√≠ se implementar√≠a la descarga en PDF
      alert('Funcionalidad de descarga PDF pr√≥ximamente');
    }
  };

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>üßæ Generar Factura</CardTitle>
          <CardDescription>
            Pedido #{orderId} - Complete la informaci√≥n del cliente
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Label htmlFor="name">Nombre Completo *</Label>
              <Input
                id="name"
                value={customerInfo.name}
                onChange={(e) => setCustomerInfo({...customerInfo, name: e.target.value})}
                placeholder="Juan P√©rez"
              />
            </div>
            <div>
              <Label htmlFor="email">Email *</Label>
              <Input
                id="email"
                type="email"
                value={customerInfo.email}
                onChange={(e) => setCustomerInfo({...customerInfo, email: e.target.value})}
                placeholder="juan@example.com"
              />
            </div>
            <div>
              <Label htmlFor="phone">Tel√©fono *</Label>
              <Input
                id="phone"
                value={customerInfo.phone}
                onChange={(e) => setCustomerInfo({...customerInfo, phone: e.target.value})}
                placeholder="+593 99 123 4567"
              />
            </div>
            <div>
              <Label htmlFor="cedula">C√©dula</Label>
              <Input
                id="cedula"
                value={customerInfo.cedula}
                onChange={(e) => setCustomerInfo({...customerInfo, cedula: e.target.value})}
                placeholder="1234567890"
              />
            </div>
            <div>
              <Label htmlFor="ruc">RUC (Empresas)</Label>
              <Input
                id="ruc"
                value={customerInfo.ruc}
                onChange={(e) => setCustomerInfo({...customerInfo, ruc: e.target.value})}
                placeholder="1234567890001"
              />
            </div>
          </div>
          
          <div>
            <Label htmlFor="address">Direcci√≥n *</Label>
            <Textarea
              id="address"
              value={customerInfo.address}
              onChange={(e) => setCustomerInfo({...customerInfo, address: e.target.value})}
              placeholder="Av. 6 de Diciembre 123, Quito, Pichincha"
            />
          </div>

          <div className="flex space-x-4">
            <Button 
              onClick={handleGenerateInvoice}
              disabled={isGenerating || !customerInfo.name || !customerInfo.email}
              className="flex-1"
            >
              {isGenerating ? 'Generando...' : 'üìÑ Generar Factura'}
            </Button>
            
            {invoice && (
              <Button 
                onClick={handleDownloadPDF}
                variant="outline"
              >
                üì• Descargar PDF
              </Button>
            )}
          </div>
        </CardContent>
      </Card>

      {invoice && (
        <Card>
          <CardHeader>
            <CardTitle>‚úÖ Factura Generada</CardTitle>
            <CardDescription>
              Factura #{invoice.invoiceNumber} creada exitosamente
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
              <div>
                <p className="font-medium">N√∫mero:</p>
                <p>{invoice.invoiceNumber}</p>
              </div>
              <div>
                <p className="font-medium">Cliente:</p>
                <p>{invoice.customerInfo.name}</p>
              </div>
              <div>
                <p className="font-medium">Total:</p>
                <p className="text-lg font-bold text-green-600">${invoice.total.toFixed(2)}</p>
              </div>
              <div>
                <p className="font-medium">Estado:</p>
                <p className="text-blue-600 capitalize">{invoice.status}</p>
              </div>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
